import React, { Component, Fragment } from 'react';
import { Alert } from 'reactstrap';
import Icon from '@mdi/react';
import { mdiAlertCircle, mdiBug } from '@mdi/js';
import * as urls from './../../../config.json';

export default class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {

        return (
            <Fragment>
                <section>
                    <h2>
                        Codesheet
                    </h2>

                    <Alert color="danger">
                        <b><Icon path={mdiAlertCircle} size={0.9} color="#ff5252" className="mr-1 mb-1" /> Be careful!</b>
                        <div>
                            Using json object written by yourself can lead to errors. Because of this reason, I recommend you to use included designer.
                            You may also use playground, which also allows you to test your json object.
                        </div>
                    </Alert>
                    <Alert color="success">
                        <b><Icon path={mdiBug} size={0.9} color="#00c853" className="mr-1 mb-1" /> Bug hunting</b>
                        <div>
                            Did you find any bug or error? Do you have any other issue or question? Please, leave me a message with informations <a href={urls['bug-hunting']}>here</a>.
                            I will try to help you ASAP.
                        </div>
                    </Alert>
                </section>
            </Fragment>
        );

    }
}